.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;

}
.checkoutProduct_image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkoutProduct_info{
    padding-left: 20px;
}
.checkoutProduct_info > button{
    background-color: #86C6F4;
    padding: 10px;
    border-radius: 10%;
    border: 1px solid white;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.checkoutProduct_title{
    font-size: 17px;
    font-weight: 800;
}
.checkoutProduct_rating{
    display: flex;
}