.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
}
.subtotal_gift{
    display: flex;
    align-items: center;

}
.subtotal_gift > input{
    margin-right: 20px;

}
.subtotal > button{
    background-color: #86C6F4;
    padding: 10px;
    border-radius: 10%;
    border: 1px solid white;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}