.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    max-width: 100%;
    background-color: white;
    z-index: -1;
}
.product_rating{
    display: flex;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin: 20px 0 0 15px;
}
.product > button{
    background-color: #86C6F4;
    padding: 10px;
    border-radius: 10%;
    border: 1px solid white;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.product_price{
    margin-top: 5px;
}
.product__info {
    height: 100px;
    margin-bottom: 15px;
}
.btn{
    cursor: pointer;
}
.btn:hover{
    filter: brightness(95%);
}